/*------------------------------------------------------------
	Slider
------------------------------------------------------------*/
.swiper {
	width: 100vw;
	height: 90vh;
	.overlay {
		width: 100vw;
		height: 100vh;
		position: absolute;
		background: rgba(0, 0, 0, 0.6);
		margin: 0;
	}
	&-image {
		position: absolute;
		width: 100vw;
		height: 100vh;
		margin: 0;
		transform: scale3d(1, 1, 1);
		-webkit-backface-visibility: hidden !important;
		backface-visibility: hidden !important;
		will-change: transform;
		transition: transform 1500ms ease;
		transition-delay: 0.4s;
	}
	.content-wrapper {
		position: relative;
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr;
		grid-template-areas: ".";
		height: 70%;
		width: 100%;
		justify-items: start;
		align-items: end;
		.content {
			padding: 1.5em;
			color: #fff;
			opacity: 0;
			width: 100%;
			max-width: 500px;
			transition-duration: 1.5s, 0.6s;
			transition-property: opacity, transform;
			transition-delay: 0.4s;
			&:first-child {
				margin: 0;
			}
		}
	}
	&-slide {
		margin: 0;
		overflow: hidden !important;
	}
	.swiper-slide.swiper-slide-active {
		.swiper-image {
			transform: scale3d(1.02, 1.02, 1);
			will-change: transform;
		}
		.content {
			opacity: 1;
		}
	}
	&-nav-wrapper {
		position: absolute;
		bottom: 6em;
		right: 6em;
		.swiper-button-next {
			right: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.5);
			width: 60px;
			height: 60px;
			&:hover {
				background: rgba(0, 0, 0, 0.8);
			}
		}
		.swiper-button-prev {
			right: 15px;
			left: auto;
			background: rgba(0, 0, 0, 0.5);
			width: 60px;
			height: 60px;
			&:hover {
				background: rgba(0, 0, 0, 0.8);
			}
		}
	}
  .button{
    display: block;
    text-decoration: none;
    margin-top: 10px;
    padding: 10px 20px;
    background-color:#f6511d;
    width: fit-content;
    color: #fff;
  }
}
