@media (min-width: 640px) {
	.swiper {
		.overlay {
			background: rgba(0, 0, 0, 0.3);
		}
		.content-wrapper {
			.content {
				padding: 0 20px;
        margin: 0 3rem !important;
        h1{
          font-size: 2rem;
        }
        p{
          font-size: 1.5rem;
          line-height: 1.3;
        }
			}
      &.content-right {
        justify-items: end;
      }
		}
		.swiper-nav-wrapper {
			bottom: 4.5em;
		}
	}
}
